.token + .row {
  padding-top: .75rem;
}
.token + .row .card {
  background-color: #fff;
}

.card {
  padding: 0;
}
.card .card-body {
  padding: 0.75rem;
}

.card.bandSeparator,
.card.partSeparator {
  background-color: #eee;
}

.card.legSeparator {
  background-color: #999;
}

.card.tokenisedSymbolBand {
  background-color: #d4edda;
}

.card.tokenisedIdBand {
  background-color: #fff3cd;
}

.card.nullBand {
  background-color: #666;
}

.card.colouredBand {
  background-color: #d1ecf1;
}

.card.error {
  background-color: #d9534f;
}

.colour .sample {
  display: inline-block;
  height: 14px;
  width: 14px;
  border: 1px solid #000;
}
